@import "./_variables";
@import "./_mixins";

.origin {
    position: relative;
    padding-top: 45px;

    &-deco {
        position: absolute;
        top: 0;
        left: -80px;
        width: 295px;
        height: 100%;
        border-radius: 3px 3px 0 0;
        background-image: linear-gradient(to bottom, #fdce63, #3abd67, #0094ac);
        opacity: 0;
    }

    &-pic {
        position: relative;
        width: 68%;
        z-index: 1;

        img {
            width: 100%;
            border-radius: 0 0 0 50px;
        }

        &::after {
            content: '';
            position: absolute;
            bottom: -31%;
            right: -50%;
            width: 76%;
            padding-bottom: 63.65%;
            background: url(./../images/bg/light/about.png) no-repeat center / contain;
            z-index: -1;
        }
    }

    &-content {
        position: relative;
        margin-left: 215px;
        padding: 50px 0 90px 50px;
        z-index: 1;

        &::before {
            content: 'THE ORIGIN';
            position: absolute;
            top: 25px;
            left: -65px;
            font-size: 34px;
            font-weight: 100;
            letter-spacing: 0.8em;
            color: #fff;
            transform-origin: center left;
            transform: rotate(90deg);
            opacity: 0.8;
        }
    }

    &-title {
        display: inline-block;
        font-size: 48px;
        font-weight: bold;
        background: linear-gradient(to right, #64c463, #0095ad);
        background: -webkit-linear-gradient(to right, #64c463, #0095ad);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
    }

    &-text {
        margin-top: 25px;
        font-size: 21px;
        line-height: 1.8;
        color: #8b8b8b;
    }

    @include breakpoint-down('desktop') {
        padding-top: 30px;

        &-deco {
            left: -70px;
            width: 285px;
        }

        &-content {
            padding: 40px 0 60px 40px;

            &::before {
                font-size: 30px;
            }
        }

        &-title {
            font-size: 40px;
        }

        &-text {
            margin-top: 15px;
            font-size: 19px;
        }
    }

    @include breakpoint-down('tablet') {
        &-deco {
            width: 200px;
        }

        &-content {
            margin-left: 130px;
            padding: 30px 0 50px 30px;

            &::before {
                font-size: 24px;
            }
        }

        &-title {
            font-size: 30px;
        }

        &-text {
            margin-top: 10px;
            font-size: 16px;
        }
    }

    @include breakpoint-down('mobile') {
        padding-top: 20px;

        &-deco {
            width: 120px;
        }

        &-pic {
            width: 100%;

            img {
                border-radius: 0 0 0 30px;
            }

            &::after {
                content: none;
            }
        }

        &-content {
            margin-left: 50px;
            padding: 20px 0 40px 20px;

            &::before {
                top: 15px;
                left: -35px;
                font-size: 20px;
            }
        }

        &-title {
            font-size: 26px;
        }
    }
}

.philosophy {
    &-title {
        position: relative;
        display: inline-block;
        padding: 5px 40px 7px 85px;
        font-size: 48px;
        font-weight: 500;
        color: #fff;
        border-radius: 0 0 40px 0;
        background-color: #72cc80;
        z-index: 1;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 100%;
            width: calc(50vw - 520px);
            height: 100%;
            background-color: #72cc80;
        }
    }

    &-pic {
        position: relative;
        margin-top: -25px;
        width: 68%;

        img {
            width: 100%;
            border-radius: 0 0 0 50px;
        }

        &::before {
            content: '';
            position: absolute;
            bottom: -31%;
            right: -50%;
            width: 76%;
            padding-bottom: 63.65%;
            background: url(./../images/bg/light/about.png) no-repeat center / contain;
            z-index: -1;
        }

        &::after {
            content: 'OUR \A CONCEPT';
            position: absolute;
            bottom: 0;
            left: calc(100% + 30px);
            font-size: 35px;
            font-weight: 100;
            letter-spacing: 0.7em;
            white-space: pre;
            color: #0093ac;
            opacity: 0.8;
        }
    }

    &-text {
        position: relative;
        margin-left: 140px;
        padding: 50px 0 75px 45px;
        font-size: 21px;
        line-height: 1.8;
        color: #8b8b8b;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 2px;
            height: 100%;
            border-radius: 1px;
            background-image: linear-gradient(to top, #fdce63, #3abd67, #0094ac);
        }
    }

    @include breakpoint-down('desktop') {
        &-title {
            font-size: 40px;

            &::before {
                width: calc(50vw - 420px);
            }
        }

        &-pic {
            &::after {
                left: calc(100% + 25px);
                font-size: 28px;
            }
        }

        &-text {
            padding: 40px 0 60px 35px;
            font-size: 19px;
        }
    }

    @include breakpoint-down('tablet') {
        &-title {
            font-size: 30px;
            border-radius: 0 0 20px 0;

            &::before {
                width: calc(50vw - 320px);
            }
        }

        &-pic {
            margin-top: -20px;

            &::after {
                left: calc(100% + 20px);
                font-size: 23px;
            }
        }

        &-text {
            margin-left: 80px;
            padding: 30px 0 45px 30px;
            font-size: 16px;
        }
    }

    @include breakpoint-down('mobile') {
        &-title {
            padding: 3px 30px 5px 75px;
            font-size: 26px;

            &::before {
                width: 20px;
            }
        }

        &-pic {
            margin-top: -15px;
            width: 100%;

            img {
                border-radius: 0 0 0 30px;
            }

            &::before {
                content: none;
            }
        }

        &-text {
            margin-left: 10px;
            padding: 20px 0 35px 20px;

            &::before {
                top: -8px;
            }
        }
    }
}